import React, { useState, useEffect, useContext } from "react";
import { Card, Button, Col, Row } from "@themesberg/react-bootstrap";
import { securedAxiosInstance } from "../../../axiosConfig";
import HtmlText from "../../components/HtmlToText/HtmlText";
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import { useHistory } from "react-router-dom";
import Loader from "../../../components/Loader";
import "./showNotifications.css";
import Icon from "./timelineIcon";
import { AuthContext } from "../../../context/AuthContext";

const ShowNotifications = () => {
  const {
    state: { profileData },
  } = useContext(AuthContext);
  const degree = profileData?.degree;
  const campus = profileData?.campus;
  const batch = profileData?.batch;

  const [start, setstart] = useState(0); //index to fetch notifications from
  const limit = 7; //no of notifications to be fetched at a time

  const [notifications, setNotifications] = useState({
    loading: false,
    data: [],
    error: null,
  });
  const [showMoreButton, setShowMoreButton] = useState(true);
  const [initialLoader, setInitialLoader] = useState(true);

  const bgColor = {
    Placement: "#5b7cf3",
    Internship: "#2cc972",
    Update: "#77d1e8",
    Webinar: "#ffc111",
    Hackathon: "#8e5bf3",
  };

  const history = useHistory();
  useEffect(() => {
    fetchNotifications();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetchNotifications = async () => {
    setNotifications({ ...notifications, loading: true });
    try {
      const response = await securedAxiosInstance.get(
        `/announcements?_sort=createdAt:desc&batch=${batch}&_where[0][degrees_allowed_contains]=${degree}&campus=${campus}&_start=${start}&_limit=${limit}`
      );
      const tempNotif = response.data;
      const newNotif = notifications.data;

      if (tempNotif.length < limit) {
        setShowMoreButton(false);
      }

      tempNotif.map((item) => newNotif.push(item));
      setNotifications({ loading: false, data: newNotif, error: null });
      setstart(start + limit);
      setInitialLoader(false);
    } catch (error) {
      setNotifications({ loading: false, data: [], error: error });
      setInitialLoader(false);
      console.log(error);
    }
  };

  function Show_button() {
    if (showMoreButton) {
      return (
        <div className="d-flex justify-content-center mt-4">
          <Button
            className="view_more"
            variant="dark"
            disabled={notifications.loading}
            onClick={fetchNotifications}
          >
            {notifications.loading ? <Loader /> : "View More"}
          </Button>
        </div>
      );
    }
  }

  function show_Notifications() {
    if (notifications.data) {
      return (
        <>
          {notifications.data.length !== 0 && (
            <VerticalTimeline animate={false} layout={"1-column-left"}>
              {/* lineColor ='#f9f9f9' */}
              {notifications.data.map((item, key) => (
                <VerticalTimelineElement
                  contentArrowStyle={{ display: "none" }}
                  contentStyle={{
                    background: "transparent",
                    boxSahdow:
                      "0px 4px 8px -2px rgb(16 24 40 / 10%), 0px 2px 4px -2px rgb(16 24 40 / 6%)",
                    transform: "translateY(-5px)",
                  }}
                  onTimelineElementClick={() => history.push(item.redirect_url === "/" ? `/companies/${item.event_id}` : item.redirect_url)}
                  icon={<Icon type={item.event_type} />}
                  iconStyle={{
                    backgroundColor: bgColor[item.event_type || "Update"],
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                  key={key}
                >
                  <Card
                    className="bg-transparent"
                    style={{ cursor: "pointer", overflowX: "hidden" }}
                  >
                    <Card.Body className="p-0">
                      <Row>
                        <Col sm={5}>
                          <div className="d-flex align-items-center">
                            <div
                              className="image bg-black rounded-circle d-flex justify-content-center align-items-center"
                              style={{
                                height: "50px",
                                width: "50px",
                                marginRight: "7%",
                                overflow: "hidden",
                                marginBottom: "7px",
                              }}
                            >
                              <div
                                className="text-logo"
                                style={{
                                  color: "white",
                                  fontSize: "1.8rem",
                                  fontWeight: "bolder",
                                  paddingTop: "0px",
                                }}
                              >
                                {item.for.split(" ").length > 1
                                  ? item.for.split(" ")[0].substring(0, 1) +
                                    item.for.split(" ")[1].substring(0, 1)
                                  : item.for.split(" ")[0].substring(0, 1)}
                              </div>
                            </div>
                            <div className="d-flex flex-column">
                              <h5>{item.title}</h5>
                              <h6 style={{ color: "#6f7274" }}>{item.for}</h6>
                            </div>
                          </div>
                        </Col>
                        <Col sm={7}>
                          <div className="d-flex flex-column align-items-start">
                            <HtmlText htmlText={item.content} />
                            <div className="d-flex w-100">
                              <div>
                                <p style={{ margin: "0px", fontWeight: "700" }}>
                                  {new Date(item.createdAt)
                                    .toString()
                                    .slice(0, 15)}
                                  ,{" "}
                                  {new Date(item.createdAt)
                                    .toString()
                                    .slice(16, 21)}
                                </p>
                              </div>
                              <div
                                className={`tag border rounded px-1 ${
                                  item.event_type || "Update"
                                }`}
                              >
                                {item.event_type || "Update"}
                              </div>
                            </div>
                          </div>
                        </Col>
                      </Row>
                    </Card.Body>
                  </Card>
                </VerticalTimelineElement>
              ))}
              {showMoreButton && (
                <VerticalTimelineElement
                  icon={
                    <img
                      src={require("../../../assets/img/icons/plus-solid.svg")}
                      alt="load_more"
                      style={{ width: "100%", height: "100%" }}
                    />
                  }
                  iconOnClick={fetchNotifications}
                  iconStyle={{ cursor: "pointer", backgroundColor: "#e44e72" }}
                ></VerticalTimelineElement>
              )}
            </VerticalTimeline>
          )}
        </>
      );
    }
  }

  function show_loader() {
    if (notifications.loading) {
      return <Loader />;
    }
  }
  if (notifications.error) {
    return (
      <div>
        <h1>Error in fetching notifications.</h1>
      </div>
    );
  }
  if (initialLoader) {
    return <Loader />;
  }

  return (
    <div>
      {show_Notifications()}
      {show_loader()}
      {Show_button()}
    </div>
  );
};

export default ShowNotifications;
