import React, { useContext, useEffect } from "react";
import ShowNotifications from "./components/showNotifications";
import { AuthContext } from "../../context/AuthContext";
import Loader from "../../components/Loader";
import toast, { Toaster } from "react-hot-toast";

const CompanyNotifications = () => {
  const { state } = useContext(AuthContext);
  const { isLoading } = state;

  useEffect(() => {
    if (state?.profileData?.banStatus === true) {
      toast.error(
        "You are banned from this platform. Please contact the admin."
      );
    }
  }, []);

  if (isLoading) return <Loader />;

  return (
    <>
      <Toaster position="top-right" reverseOrder={false} />
      <div className="d-flex justify-content-center align-items-center flex-column w-80 mt-3 bg-white">
        <div
          className="w-100"
          style={{ paddingTop: "10px", paddingBottom: "10px" }}
        >
          <ShowNotifications />
        </div>
      </div>
    </>
  );
};

export default CompanyNotifications;
